<template>
  <el-card>
    <el-row slot="header">
      <el-button type="primary" @click="$router.push('/questions/new')">新增试题</el-button>
      <el-button type="primary">批量导入</el-button>
    </el-row>
    <el-form :model="formData" ref="myForm" inline label-width="80px">
      <el-form-item label="学科" prop="subjectID">
        <el-select v-model="formData.subjectID">
          <el-option
            v-for="item in subjects"
            :key="item.value"
            :value="item.value"
            :label="item.label"
          ></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="难度" prop="difficulty">
        <el-select v-model="formData.difficulty">
          <el-option
            v-for="item in difficulty"
            :key="item.value"
            :value="item.value"
            :label="item.label"
          ></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="试题类型" prop="questionType">
        <el-select v-model="formData.questionType">
          <el-option
            v-for="item in questionType"
            :key="item.value"
            :value="item.value"
            :label="item.label"
          ></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="标签" prop="tags">
        <el-select v-model="formData.tags">
          <el-option v-for="item in tags" :key="item.value" :value="item.value" :label="item.label"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="城市" prop="province">
        <el-select v-model="formData.province">
          <el-option v-for="(item,index) in provinces" :key="index" :label="item" :value="item"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="区域" prop="city">
        <el-select v-model="formData.city">
          <el-option v-for="(item,index) in myCitys" :key="index" :label="item" :value="item"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="关键字" prop="keyword">
        <el-input v-model="formData.keyword"></el-input>
      </el-form-item>
      <el-form-item label="题目备注" prop="remarks">
        <el-input v-model="formData.remarks"></el-input>
      </el-form-item>
      <el-form-item label="企业简称" prop="shortName">
        <el-input v-model="formData.shortName"></el-input>
      </el-form-item>
      <el-form-item label="方向" prop="direction">
        <el-select v-model="formData.direction">
          <el-option v-for="(item,index) in direction" :key="index" :value="item" :label="item"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="录入人" prop="creatorID">
        <el-select v-model="formData.creatorID">
          <el-option v-for="(item) in users" :key="item.id" :value="item.id" :label="item.username"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="二级目录" prop="catalogID">
        <el-select v-model="formData.catalogID">
          <el-option
            v-for="(item) in directioy"
            :key="item.id"
            :value="item.value"
            :label="item.label"
          ></el-option>
        </el-select>
      </el-form-item>
    </el-form>
    <el-row type="flex" justify="center" style="margin: 20px 0">
      <el-button @click="clear">清除</el-button>
      <el-button @click="search" type="primary">搜索</el-button>
    </el-row>
    <el-table :data="list">
      <el-table-column label="序号" prop="id"></el-table-column>
      <el-table-column label="试题编号" prop="number"></el-table-column>
      <el-table-column label="学科" prop="subjectID"></el-table-column>
      <el-table-column label="题型" :formatter="questionFormatter" prop="questionType"></el-table-column>
      <el-table-column width="200px" label="题干" prop="question"></el-table-column>
      <el-table-column label="录入时间" prop="addDate" width="180px">
        <template slot-scope="obj">{{ obj.row.addDate | parseTimeByString}}</template>
      </el-table-column>
      <el-table-column label="难度" prop="city"></el-table-column>
      <el-table-column label="录入人" prop="creator"></el-table-column>
      <el-table-column label="操作" width="180px">
        <template slot-scope="obj">
          <el-button type="text" @click="visible=true" size="small">预览</el-button>
          <el-button type="text" size="small" @click="modify(obj.row.id)">修改</el-button>
          <el-button type="text" @click="delItem(obj.row.id)" size="small">删除</el-button>
          <el-button type="text" @click="joinItem(obj.row.id)" size="small">加入精选</el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-row type="flex" justify="end" style="height:60px" align="middle">
      <el-pagination
        background
        :page-size="page.pageSize"
        :current-page="page.currentPage"
        layout="total, prev, pager, next"
        :total="page.total"
        @current-change="changePage"
      ></el-pagination>
    </el-row>
    <preFlash :visible.sync="visible" />
  </el-card>
</template>

<script>
import { list, remove } from "../../api/hmmm/questions";
import { simple as TagList } from "../../api/hmmm/tags";
import { difficulty, questionType, direction } from "../../api/hmmm/constants";
import { simple as subjectList } from "../../api/hmmm/subjects";
import { simple as UserList } from "../../api/base/users";
import { simple as directioy } from "../../api/hmmm/directorys";
import { citys, provinces } from "../../api/hmmm/citys";
import preFlash from "./pre-flash";
export default {
  components: {
    preFlash
  },
  data() {
    return {
      visible: false,
      list: [],
      subjects: [],
      tags: [],
      direction,
      difficulty,
      questionType,
      directioy,
      citys: [],
      provinces: provinces(),
      users: [],
      formData: {
        difficulty: null,
        questionType: null,
        tags: null,
        subjectID: null,
        province: null,
        city: null,
        keyword: null,
        remarks: null,
        shortName: null,
        direction: null,
        creatorID: null,
        catalogID: null
      },
      page: {
        currentPage: 1,
        pageSize: 10,
        total: 0
      }
    };
  },
  computed: {
    myCitys() {
      return citys(this.formData.province);
    }
  },
  methods: {
    modify(id) {
      this.$router.push({ path: "/questions/new", query: { id } });
    },
    // 删除文章
    async delItem(id) {
      await this.$confirm("是否删除此文章");
      await remove({ id });
      this.$message({ type: "success", message: "删除成功" });
      this.getCondition();
    },
    async joinItem(id) {
      await this.$confirm("是否加入精选");
    },
    changePage(newPage) {
      this.page.currentPage = newPage;
      this.getCondition();
    },

    getCondition() {
      let params = {
        page: this.page.currentPage,
        pageSize: this.page.pageSize,
        ...this.formData
      };
      this.getQuestion(params);
    },
    questionFormatter(row, column, cellValue) {
      let result = this.questionType.filter(item => item.value == cellValue);
      return result.length ? result[0].label : "未知";
    },
    search() {
      this.page.currentPage = 1;
      this.getCondition();
    },
    clear() {
      this.$refs.myForm.resetFields();
    },
    async getSubject() {
      let result = await subjectList();
      this.subjects = result.data;
    },
    async getQuestion(data) {
      let result = await list(data);
      this.list = result.data.items;
      this.page.total = result.data.counts;
    },
    async getTags() {
      let result = await TagList();
      this.tags = result.data;
    },
    async getUserList() {
      let result = await UserList();
      this.users = result.data;
    },
    async getDirectioy() {
      let result = await directioy();
      this.directioy = result.data;
    }
  },
  async created() {
    this.getDirectioy();
    this.getUserList();
    this.getTags();
    this.getSubject();
    this.getQuestion();
  }
};
</script>

<style lang="scss"></style>
