<template>
  <el-dialog :visible="visible" @close="$emit('update:visible',false)" title="题目预览">
    <el-row>
      <el-col :span="6">[题库]:</el-col>
      <el-col :span="6"></el-col>
      <el-col :span="6"></el-col>
      <el-col :span="6">[学科]:</el-col>
    </el-row>
    <el-row>
      <el-col :span="6">[题型]:</el-col>
      <el-col :span="6">[编号]:</el-col>
      <el-col :span="6">[难度]:</el-col>
      <el-col :span="6">[标签]:</el-col>
    </el-row>
    <el-row>
      <el-col :span="6">[目录]:</el-col>
      <el-col :span="6">[企业]:</el-col>
      <el-col :span="6">[方向]:</el-col>
      <el-col :span="6">[城市]:</el-col>
    </el-row>
    <el-row></el-row>
    <el-row slot="footer" type="flex" justify="end">
      <el-button type="primary" @click="$emit('update:visible',false)">关闭</el-button>
    </el-row>
  </el-dialog>
</template>

<script>
import { detail } from "../../api/hmmm/questions";

export default {
  props: ["visible", "id"],
  created() {
    this.id && this.getDetail(this.id);
  },
  methods: {
    async getDetail(id) {
      let result = await detail({ id });
      this.formData = result.data;
    }
  },
  data() {
    return {
      formData: {
        options: [],
        videoURL: "",
        answer: "",
        question: "",
        difficulty: "1",
        questionType: "1",
        tags: null,
        subjectID: null,
        province: null,
        city: null,
        keyword: null,
        remarks: "",
        shortName: null,
        direction: null,
        catalogID: null,
        enterpriseID: null
      }
    };
  }
};
</script>

